<template>
  <v-app>
    <v-navigation-drawer
      temporary
      right
      v-model="drawer"
      class="grey lighten-4"
      fixed
      app
    >
    <v-list
       dense
       class="grey lighten-4"
     >
       <template>
         <v-layout
           row
           align-center
         >
           <v-flex xs6>
             <v-subheader
              class="grey--text"
             >
               Filters
             </v-subheader>
           </v-flex>
         </v-layout>
         <v-divider
           dark
           class="mt-0 mb-2"
         ></v-divider>
         <v-text-field
           solo-inverted
           flat
           hide-details
           label="Search"
           prepend-inner-icon="search"
           v-model="searchFilter"
           v-on:keyup="filterCharadata()"
         ></v-text-field>
         <v-layout row>
           <v-flex xs10 class="pl-3 mt-2">
             <v-select
               :items="characterNames"
               v-model="characterToFilter"
               v-on:change="handleSelect(); filterCharadata()"
               label="Filter by character"
             >
             <template slot="selection" slot-scope="data">
              <v-flex xs2>
                <v-avatar tile size="30px">
                  <img :src="'static/img/unique/'+ data.item +'.png'"/>
                </v-avatar>
              </v-flex>
              <v-flex class='ml-2'>
                {{ data.item }}
              </v-flex>
            </template>
            <template slot="item" slot-scope="data">
              <v-list-tile-avatar tile>
                <img :src="'static/img/unique/'+ data.item +'.png'"/>
              </v-list-tile-avatar>
              <v-list-tile-content>
                <v-list-tile-title v-html="data.item"></v-list-tile-title>
              </v-list-tile-content>
            </template>

           </v-select>
           </v-flex>
           <v-flex xs2 class="text-xs-center">
             <v-btn flat icon class="mt-3" @click="characterToFilter = null; filters.characterFilter = false; filterCharadata()"><v-icon>clear</v-icon></v-btn>
           </v-flex>
         </v-layout>


         <v-flex xs6>
           <v-subheader
            class="grey--text"
           >
            Attack Type
           </v-subheader>
         </v-flex>
         <v-layout
          row
          >
            <v-spacer/>
            <v-flex v-if="!filters.normalFilter">
              <img src="static/img/filter/normal_off.png"/>
            </v-flex>
            <v-flex v-else>
              <img src="static/img/filter/normal.png"/>
            </v-flex>
            <v-flex>
              <v-btn
                small
                v-bind:outline="!filters.normalFilter"
                v-on:click="filters.normalFilter = !filters.normalFilter; filterCharadata();"
                color="cyan"
                class="ml-0 white--text">Normal
              </v-btn>
            </v-flex>
            <v-flex v-if="!filters.specialFilter">
                <img src="static/img/filter/special_off.png"/>
            </v-flex>
            <v-flex v-else>
              <img src="static/img/filter/special.png"/>
            </v-flex>
            <v-flex>
              <v-btn
                small
                v-bind:outline="!filters.specialFilter"
                v-on:click="filters.specialFilter = !filters.specialFilter; filterCharadata()"
                color="pink darken-1"
                class="ml-0 white--text">Special
              </v-btn>
            </v-flex>
            <v-spacer/>
        </v-layout>
        <v-layout row>
           <v-subheader
            class="grey--text"
            v-if="hover.element"
           >
             Element
           </v-subheader>
           <v-subheader
            class="red--text"
            v-if="hover.flower"
           >
             Flower
           </v-subheader>
           <v-subheader
            class="green--text"
            v-if="hover.wind"
           >
             Wind
           </v-subheader>
           <v-subheader
            class="blue--text"
            v-if="hover.snow"
           >
             Snow
           </v-subheader>
           <v-subheader
            class="pink--text"
            v-if="hover.cloud"
           >
             Cloud
           </v-subheader>
           <v-subheader
            class="orange--text"
            v-if="hover.moon"
           >
             Moon
           </v-subheader>
           <v-subheader
            class="purple--text"
            v-if="hover.cosmos"
           >
             Cosmos
           </v-subheader>
           <v-subheader
            class="black--text"
            v-if="hover.dream"
           >
             Dream
           </v-subheader>
       </v-layout>
         <v-layout row>
           <v-spacer/>
           <v-flex v-if="!filters.flowerFilter">
             <img src="static/img/filter/flower_off.png" height="35"
             @click="filters.flowerFilter = !filters.flowerFilter; filterCharadata()"
             @mouseover="hover.flower = !hover.flower; hover.element = !hover.element"
             @mouseout="hover.flower = !hover.flower; hover.element = !hover.element"
             />
           </v-flex>
           <v-flex v-else>
             <img src="static/img/filter/flower.png" height="35"
             @click="filters.flowerFilter = !filters.flowerFilter; filterCharadata()"
             @mouseover="hover.flower = !hover.flower; hover.element = !hover.element"
             @mouseout="hover.flower = !hover.flower; hover.element = !hover.element"/>
           </v-flex>
           <v-flex v-if="!filters.windFilter">
             <img src="static/img/filter/wind_off.png" height="35"
             @click="filters.windFilter = !filters.windFilter; filterCharadata()"
             @mouseover="hover.wind = !hover.wind; hover.element = !hover.element"
             @mouseout="hover.wind = !hover.wind; hover.element = !hover.element"/>
           </v-flex>
           <v-flex v-else>
             <img src="static/img/filter/wind.png" height="35"
             @click="filters.windFilter = !filters.windFilter; filterCharadata()"
             @mouseover="hover.wind = !hover.wind; hover.element = !hover.element"
             @mouseout="hover.wind = !hover.wind; hover.element = !hover.element"/>
           </v-flex>
           <v-flex v-if="!filters.snowFilter">
             <img src="static/img/filter/snow_off.png" height="35"
             @click="filters.snowFilter = !filters.snowFilter; filterCharadata()"
             @mouseover="hover.snow = !hover.snow; hover.element = !hover.element"
             @mouseout="hover.snow = !hover.snow; hover.element = !hover.element"/>
           </v-flex>
           <v-flex v-else>
             <img src="static/img/filter/snow.png" height="35"
             @click="filters.snowFilter = !filters.snowFilter; filterCharadata()"
             @mouseover="hover.snow = !hover.snow; hover.element = !hover.element"
             @mouseout="hover.snow = !hover.snow; hover.element = !hover.element"/>
           </v-flex>
           <v-flex v-if="!filters.cloudFilter">
             <img src="static/img/filter/cloud_off.png" height="35"
             @click="filters.cloudFilter = !filters.cloudFilter; filterCharadata()"
             @mouseover="hover.cloud = !hover.cloud; hover.element = !hover.element"
             @mouseout="hover.cloud = !hover.cloud; hover.element = !hover.element"/>
           </v-flex>
           <v-flex v-else>
             <img src="static/img/filter/cloud.png" height="35"
             @click="filters.cloudFilter = !filters.cloudFilter; filterCharadata()"
             @mouseover="hover.cloud = !hover.cloud; hover.element = !hover.element"
             @mouseout="hover.cloud = !hover.cloud; hover.element = !hover.element"/>
           </v-flex>
           <v-flex v-if="!filters.moonFilter">
             <img src="static/img/filter/moon_off.png" height="35"
             @click="filters.moonFilter = !filters.moonFilter; filterCharadata()"
             @mouseover="hover.moon = !hover.moon; hover.element = !hover.element"
             @mouseout="hover.moon = !hover.moon; hover.element = !hover.element"/>
           </v-flex>
           <v-flex v-else>
             <img src="static/img/filter/moon.png" height="35"
             @click="filters.moonFilter = !filters.moonFilter; filterCharadata()"
             @mouseover="hover.moon = !hover.moon; hover.element = !hover.element"
             @mouseout="hover.moon = !hover.moon; hover.element = !hover.element"/>
           </v-flex>
           <v-flex v-if="!filters.cosmosFilter">
             <img src="static/img/filter/cosmos_off.png" height="35"
             @click="filters.cosmosFilter = !filters.cosmosFilter; filterCharadata()"
             @mouseover="hover.cosmos = !hover.cosmos; hover.element = !hover.element"
             @mouseout="hover.cosmos = !hover.cosmos; hover.element = !hover.element"/>
           </v-flex>
           <v-flex v-else>
             <img src="static/img/filter/cosmos.png" height="35"
             @click="filters.cosmosFilter = !filters.cosmosFilter; filterCharadata()"
             @mouseover="hover.cosmos = !hover.cosmos; hover.element = !hover.element"
             @mouseout="hover.cosmos = !hover.cosmos; hover.element = !hover.element"/>
           </v-flex>
           <v-flex v-if="!filters.dreamFilter">
             <img src="static/img/filter/dream_off.png" height="35"
             @click="filters.dreamFilter = !filters.dreamFilter; filterCharadata()"
             @mouseover="hover.dream = !hover.dream; hover.element = !hover.element"
             @mouseout="hover.dream = !hover.dream; hover.element = !hover.element"/>
           </v-flex>
           <v-flex v-else>
             <img src="static/img/filter/dream.png" height="35"
             @click="filters.dreamFilter = !filters.dreamFilter; filterCharadata()"
             @mouseover="hover.dream = !hover.dream; hover.element = !hover.element"
             @mouseout="hover.dream = !hover.dream; hover.element = !hover.element"/>
           </v-flex>
           <v-spacer/>
         </v-layout>

         <v-subheader
          class="grey--text"
         >
           Position
         </v-subheader>

         <v-layout row>
           <v-spacer/>
           <v-flex v-if="!filters.backFilter" class="text-xs-center">
             <img src="static/img/filter/back_off.png" height="35"
             @click="filters.backFilter = !filters.backFilter; filterCharadata()"/>
           </v-flex>
           <v-flex v-else class="text-xs-center" >
             <img src="static/img/filter/back.png" height="35"
             @click="filters.backFilter = !filters.backFilter; filterCharadata()"/>
           </v-flex>

           <v-flex v-if="!filters.middleFilter" class="text-xs-center">
             <img src="static/img/filter/middle_off.png" height="35"
             @click="filters.middleFilter = !filters.middleFilter; filterCharadata()"/>
           </v-flex>
           <v-flex v-else class="text-xs-center" >
             <img src="static/img/filter/middle.png" height="35"
             @click="filters.middleFilter = !filters.middleFilter; filterCharadata()"/>
           </v-flex>

           <v-flex v-if="!filters.frontFilter" class="text-xs-center" >
             <img src="static/img/filter/front_off.png" height="35"
             @click="filters.frontFilter = !filters.frontFilter; filterCharadata()"/>
           </v-flex>
           <v-flex v-else class="text-xs-center" >
             <img src="static/img/filter/front.png" height="35"
             @click="filters.frontFilter = !filters.frontFilter; filterCharadata()"/>
           </v-flex>
           <v-spacer/>
         </v-layout>


         <v-subheader
          class="grey--text"
         >
           Rarity
         </v-subheader>
         <v-layout row>
           <v-spacer/>
           <v-flex class="text-xs-center" v-if="!filters.star2Filter">
             <img src="static/img/filter/star2_off.png" height="25" class="mt-2"
             @click="
                filters.star2Filter = !filters.star2Filter; filterCharadata()
              "/>
           </v-flex>
           <v-flex class="text-xs-center" v-else>
             <img src="static/img/filter/star2.png" height="25" class="mt-2"
             @click="
                filters.star2Filter = !filters.star2Filter; filterCharadata()
              "/>
           </v-flex>

           <v-flex class="text-xs-center" v-if="!filters.star3Filter" >
             <img src="static/img/filter/star3_off.png" height="25" class="mt-2"
             @click="
                filters.star3Filter = !filters.star3Filter; filterCharadata()
              "/>
           </v-flex>
           <v-flex class="text-xs-center" v-else>
             <img src="static/img/filter/star3.png" height="25" class="mt-2"
             @click="
                filters.star3Filter = !filters.star3Filter; filterCharadata()
              "/>
           </v-flex>

           <v-flex class="text-xs-center" v-if="!filters.star4Filter">
             <img src="static/img/filter/star4_off.png" height="25" class="mt-2"
             @click="
                filters.star4Filter = !filters.star4Filter; filterCharadata()
              "/>
           </v-flex>
           <v-flex class="text-xs-center" v-else>
             <img src="static/img/filter/star4.png" height="25" class="mt-2"
             @click="
                filters.star4Filter = !filters.star4Filter; filterCharadata()
              "/>
           </v-flex>

           <v-spacer/>
         </v-layout>

         <v-subheader
          class="grey--text"
          v-if="hover.school"
         >
           School
         </v-subheader>
         <v-subheader
          class="red--text"
          v-if="hover.seisho"
         >
           Seisho Music Academy
         </v-subheader>
         <v-subheader
          class="purple--text"
          v-if="hover.rinmeikan"
         >
           Rinmeikan Girl's Academy
         </v-subheader>
         <v-subheader
          class="orange--text"
          v-if="hover.frontier"
         >
           Frontier Arts Academy
         </v-subheader>
         <v-subheader
          class="blue--text"
          v-if="hover.seekfelt"
         >
           Seekfelt Music Institute
         </v-subheader>
         <v-layout row>
           <v-spacer/>
           <v-flex class="text-xs-center" v-if="!filters.seishoFilter">
             <img src="static/img/filter/seisho_off.png" height="50" class="mt-2"
             @click="filters.seishoFilter = !filters.seishoFilter; filterCharadata()"
             @mouseover="hover.seisho = !hover.seisho; hover.school = !hover.school"
             @mouseout="hover.seisho = !hover.seisho; hover.school = !hover.school"/>
           </v-flex>
           <v-flex class="text-xs-center" v-else>
             <img src="static/img/filter/seisho.png" height="50" class="mt-2"
             @click="filters.seishoFilter = !filters.seishoFilter; filterCharadata()"
             @mouseover="hover.seisho = !hover.seisho; hover.school = !hover.school"
             @mouseout="hover.seisho = !hover.seisho; hover.school = !hover.school"/>
           </v-flex>

           <v-flex class="text-xs-center" v-if="!filters.rinmeikanFilter">
             <img src="static/img/filter/rinmeikan_off.png" height="50" class="mt-2"
             @click="filters.rinmeikanFilter = !filters.rinmeikanFilter; filterCharadata()"
             @mouseover="hover.rinmeikan = !hover.rinmeikan; hover.school = !hover.school"
             @mouseout="hover.rinmeikan = !hover.rinmeikan; hover.school = !hover.school"/>
           </v-flex>
           <v-flex class="text-xs-center" v-else>
             <img src="static/img/filter/rinmeikan.png" height="50" class="mt-2"
             @click="filters.rinmeikanFilter = !filters.rinmeikanFilter; filterCharadata()"
             @mouseover="hover.rinmeikan = !hover.rinmeikan; hover.school = !hover.school"
             @mouseout="hover.rinmeikan = !hover.rinmeikan; hover.school = !hover.school"/>
           </v-flex>

           <v-flex class="text-xs-center" v-if="!filters.frontierFilter">
             <img src="static/img/filter/frontier_off.png" height="50" class="mt-2"
             @click="filters.frontierFilter = !filters.frontierFilter; filterCharadata()"
             @mouseover="hover.frontier = !hover.frontier; hover.school = !hover.school"
             @mouseout="hover.frontier = !hover.frontier; hover.school = !hover.school"/>
           </v-flex>
           <v-flex class="text-xs-center" v-else>
             <img src="static/img/filter/frontier.png" height="50" class="mt-2"
             @click="filters.frontierFilter = !filters.frontierFilter; filterCharadata()"
             @mouseover="hover.frontier = !hover.frontier; hover.school = !hover.school"
             @mouseout="hover.frontier = !hover.frontier; hover.school = !hover.school"/>
           </v-flex>

           <v-flex class="text-xs-center" v-if="!filters.seekfeltFilter">
             <img src="static/img/filter/seekfelt_off.png" height="50" class="mt-2"
              @click="filters.seekfeltFilter = !filters.seekfeltFilter; filterCharadata()"
              @mouseover="hover.seekfelt = !hover.seekfelt; hover.school = !hover.school"
              @mouseout="hover.seekfelt = !hover.seekfelt; hover.school = !hover.school"/>
           </v-flex>
           <v-flex class="text-xs-center" v-else>
             <img src="static/img/filter/seekfelt.png" height="50" class="mt-2"
             @click="filters.seekfeltFilter = !filters.seekfeltFilter; filterCharadata()"
             @mouseover="hover.seekfelt = !hover.seekfelt; hover.school = !hover.school"
             @mouseout="hover.seekfelt = !hover.seekfelt; hover.school = !hover.school"/>
           </v-flex>

           <v-spacer/>
         </v-layout>

         <v-layout row class="pt-4">
           <v-flex class="text-xs-center">
             <v-btn outline color="green" @click="resetFilters()"><v-icon>refresh</v-icon>  Reset</v-btn>
           </v-flex>
         </v-layout>

       </template>
     </v-list>
    </v-navigation-drawer>
    <v-toolbar fixed app clipped-left clipped-right color="amber">
      <v-toolbar-side-icon dark></v-toolbar-side-icon>
      <v-toolbar-title v-text="title" class="white--text"></v-toolbar-title>
      <v-spacer/>
      <v-btn flat class="white--text"  @click.stop="drawer = !drawer">
        <v-icon>filter_list</v-icon> Filters
      </v-btn>
    </v-toolbar>
    <v-content>
      <v-container fluid>

        <v-slide-y-transition mode="out-in">
          <v-layout align-center>
            <v-flex md2> </v-flex>
            <v-flex xs12 md8 style="z-index: 1">

            <v-data-table
              :headers="headers"
              :items="filteredTable"
              class="elevation-1"
              hide-actions
              item-key="subname"
            >
              <template slot="items" slot-scope="props">
                <tr @click="props.expanded = !props.expanded">
                  <td><div class="text-xs-left" >
                    <v-flex style="position: relative">
                      <v-btn v-model="pictureToDisplay" flat class="amber--text" style="height: 50px; width: 100%; position:absolute; bottom: 0; right: -3px;"
                      @click.stop="$set(imageDialog, props.item.ID, true)"
                      >
                      <v-icon style="position:absolute; top: 5px; left: 1px;">add</v-icon>
                    </v-btn>
                      <v-dialog
                        v-model="imageDialog[props.item.ID]"
                        width="800px"
                        >
                        <v-card>
                          <v-img :src="'static/img/chara/' + props.item.ID + '.png'">
                          </v-img>
                          <v-card-actions>
                          <v-spacer></v-spacer>
                          <v-btn
                            color="primary"
                            flat
                            @click.stop="$set(imageDialog, props.item.ID, false)"
                          >
                            close
                          </v-btn>
                          </v-card-actions>
                        </v-card>
                      </v-dialog>

                      <img :src="'static/img/chara/1_' + props.item.ID + '.png'" height="50" class="mt-1"/>
                    </v-flex>
                    <v-flex>
                      <img :src="'static/img/filter/icon_rarity_star_' + props.item.rarity + '.png'" height="15"/>
                    </v-flex>
                  </div></td>
                  <td class="text-xs-left">
                    <v-layout row>
                      <v-flex>
                        <h4>{{ props.item.subname }}</h4>
                      </v-flex>
                    </v-layout>
                    <v-layout row>
                      <v-flex>
                        <h5 style="font-weight: normal;">{{ props.item.charaname }}</h5>
                      </v-flex>
                    </v-layout>


                  </td>
                  <td class="text-xs-center">
                      <img :src="'static/img/filter/' + props.item.school + '.png'" height="40" class="mt-1"/></td>
                  <td class="text-xs-center">
                      <img :src="'static/img/filter/' + props.item.element + '.png'" height="25" class="mt-2"/></td>
                  <td class="text-xs-center">
                      <img :src="'static/img/filter/' + props.item.atktype + '.png'" height="25" class="mt-2"/>
                  </td>
                  <td class="text-xs-center">
                      <img :src="'static/img/filter/' + props.item.position + '.png'" height="25" class="mt-2"/>
                  </td>
                  <td class="text-xs-left">{{ props.item.overall }}</td>
                  <td class="text-xs-left">{{ props.item.hp }}</td>
                  <td class="text-xs-left">{{ props.item.atk }}</td>
                  <td class="text-xs-left">{{ props.item.def }}</td>
                  <td class="text-xs-left">{{ props.item.mdef }}</td>
                  <td class="text-xs-left">{{ props.item.spd }}</td>

              </tr>
              </template>
              <template slot="no-data">
                <v-alert :value="true" color="error" icon="warning">
                  <v-layout row>
                    <v-flex xs3>
                      <img :src="'static/img/noresults.png'" height="150"/>
                    </v-flex>

                    <v-flex xs9>
                      <v-layout align-start justify-center column fill-height>
                        <v-spacer/>
                        <v-flex>
                          <h2 class="mt-4">No results found...</h2>
                        </v-flex>
                        <v-spacer/>
                      </v-layout>

                    </v-flex>
                  </v-layout>
                </v-alert>
              </template>

              <template slot="expand" slot-scope="props">
                <v-card color="grey lighten-2 py-2">
                  <v-layout row>
                    <v-flex xs1 class="text-xs-center white--text mx-2">
                      <div class="mt-2" style="max-width: 100px; background-color: grey; border-radius: 25px;"><h4 class="">Skills</h4></div>
                    </v-flex>
                    <v-flex xs4 md3 class="mx-1" style="border-style: outset; border-width: 2px; border-color: #E0E0E0; border-radius: 12px;">
                      <v-layout column>
                        <v-flex xs12 >
                          <v-card  class="white--text" style="border-radius: 12px 12px 0px 0px;">
                            <v-layout>
                              <v-flex xs8 style="background-color: #FF5156; border-radius: 12px 0px 30px 0px;">
                                <h3 class="ml-3">{{ props.item.skill1name }}</h3>
                              </v-flex>
                              <v-spacer/>
                              <v-flex xs2 class="text-xs-center my-1 mr-2">
                                <div style="background-color: grey; border-radius: 25px;"><h4 class="">AP 2</h4></div>
                              </v-flex>
                            </v-layout>
                          </v-card>
                        </v-flex>
                        <v-flex xs4>
                          <v-card flat color="grey lighten-3" class="grey--text text--darken-1" height="50px" style="border-radius: 0px 0px 12px 12px ;">
                            <v-layout row>
                              <v-flex xs12 class="text-xs-left mt-2">
                                <h4 class="ml-3">{{ props.item.skill1effect }}</h4>
                              </v-flex>
                            </v-layout>
                          </v-card>
                        </v-flex>
                    </v-layout>
                    </v-flex>

                    <v-flex xs4 md3 class="mx-1" style="border-style: outset; border-width: 2px; border-color: #E0E0E0; border-radius: 12px;">
                      <v-layout column>
                        <v-flex xs12 >
                          <v-card  class="white--text" style="border-radius: 12px 12px 0px 0px;">
                            <v-layout>
                              <v-flex xs8 style="background-color: #FF5156; border-radius: 12px 0px 30px 0px;">
                                <h3 class="ml-3">{{ props.item.skill2name }}</h3>
                              </v-flex>
                              <v-spacer/>
                              <v-flex xs2 class="text-xs-center my-1 mr-2">
                                <div style="background-color: grey; border-radius: 25px;"><h4 class="">AP 3</h4></div>
                              </v-flex>
                            </v-layout>
                          </v-card>
